import React, { useState } from "react";
import { makeStyles, Paper } from "@material-ui/core";
import Carousel from "react-spring-3d-carousel";
import Image1 from "../../Resources/Images/Image1.jpg";
import Image2 from "../../Resources/Images/Image2.jpg";
import Image3 from "../../Resources/Images/Image3.jpg";
import Image4 from "../../Resources/Images/Image4.jpg";
import { config } from "@react-spring/core";

const useStyles = makeStyles(() => ({
  paperContainer: {
    width: 300,
    height: 400,
    borderRadius: 28,
  },
  imgStyle: {
    width: "100%",
    height: "100%",
    backgroundRepeat: "no-repeat",
  },
}));

function Custom3dCarousel() {
  const [goToSlide, setgoToSlide] = useState(0);
  const classes = useStyles();

  const slides = [
    {
      key: 0,
      content: (
        <Paper elevation={12} className={classes.paperContainer}>
          <img src={Image1} alt="something" className={classes.imgStyle} />
        </Paper>
      ),
    },
    {
      key: 1,
      content: (
        <Paper elevation={12} className={classes.paperContainer}>
          <img src={Image2} alt="something" className={classes.imgStyle} />
        </Paper>
      ),
    },
    {
      key: 2,
      content: (
        <Paper elevation={12} className={classes.paperContainer}>
          <img src={Image3} alt="something" className={classes.imgStyle} />
        </Paper>
      ),
    },
    {
      key: 3,
      content: (
        <Paper elevation={12} className={classes.paperContainer}>
          <img src={Image4} alt="something" className={classes.imgStyle} />
        </Paper>
      ),
    },
  ].map((slide, index) => {
    return { ...slide, onClick: () => setgoToSlide(index) };
  });
  return (
    <Carousel
      slides={slides}
      goToSlide={goToSlide}
      offsetRadius={1}
      animationConfig={{ tension: 200, friction: 10 }}
    />
  );
}

export default Custom3dCarousel;
